































































import Vue from 'vue';
import { mapActions } from 'vuex';
import FormattedPayload from '../molecules/formatted-payload.vue';
import ScenarioSelector from '../molecules/scenario-selector.vue';
import Tag from '../atoms/tag.vue';
import Icon from '../atoms/icon.vue';
import { LogEntryLocal } from '../../@types/events';

export default Vue.extend({
    props: {
        logEntry: {
            type: Object as () => LogEntryLocal,
        },
    },
    computed: {
        routeClass(): string {
            switch (this.logEntry.method) {
                case 'POST':
                    return 'post';
                case 'GET':
                    return 'get';
                case 'PUT':
                    return 'put';
                case 'PATCH':
                    return 'patch';
                case 'DELETE':
                    return 'delete';
                default:
                    return '';
            }
        },
        color(): string {
            switch (this.logEntry.method) {
                case 'POST':
                    return 'blue';
                case 'GET':
                    return 'green';
                case 'PUT':
                    return 'purple';
                case 'PATCH':
                    return 'yellow';
                case 'DELETE':
                    return 'red';
                default:
                    return '';
            }
        },
        noRouteMatch(): boolean {
            return (
                (this.logEntry.error &&
                    this.logEntry.error.code == 'NO_ROUTE_MATCH') ||
                false
            );
        },
        noScenarioMatch(): boolean {
            return (
                (this.logEntry.error &&
                    this.logEntry.error.code == 'NO_SCENARIO_MATCH') ||
                false
            );
        },
        intercepted(): boolean {
            return this.logEntry.scenarios !== undefined;
        },
        caseMatch(): boolean {
            return !this.logEntry.error && !this.intercepted;
        },
    },
    methods: {
        ...mapActions(['resolveRouteInterception']),
        handleScenarioSelected(scenario: {
            id: string;
            group?: string;
            name: string;
        }) {
            this.resolveRouteInterception({
                logEntryId: this.logEntry.id,
                scenarioId: scenario.id,
            });
        },
    },
    components: {
        vTag: Tag,
        vIcon: Icon,
        vFormattedPayload: FormattedPayload,
        vScenarioSelector: ScenarioSelector,
    },
});
