




















import Vue from 'vue';
import { mapGetters } from 'vuex';
import pkg from '../../../package.json';
const Logo = require('@/assets/svg/logo.svg') as string;

export default Vue.extend({
    data() {
        return {
            logo: Logo
        };
    },
    computed: {
        ...mapGetters(['connected']),
        version() {
            return pkg?.version;
        }
    }
});
